.Validform_checktip {
margin-left: 8px;
line-height: 20px;
height: 20px;
overflow: hidden;
color: #999;
font-size: 12px;
position: absolute;
top: -35px;
left: -63px;
z-index: 2;
height: 30px;
background-color: #fff;
}

.valid-arrow-bottom {
display: none;
position: absolute;
z-index: 2;
width: 0;
height: 0;
top: -6px;
left: 6px;
border: 6px solid red;
border-right-color: transparent;
border-left-color: transparent;
border-bottom-color: transparent;
}
