.valid-arrow-bottom:after {
content: '\00a0';
position: absolute;
width: 0;
height: 0;
border: 8px solid #fff;
right: -8px;
top: -9px;
border-right-color: transparent;
border-left-color: transparent;
border-bottom-color: transparent;
}

.Validform_wrong {
color: red;
padding: 0 5px 0 5px;
white-space: nowrap;
border: solid 1px red;
height: 30px;
line-height: 30px;
min-width: 80px;
text-align: center;
}


