//高度34px  2.125em
//字体12px  .75em
//上下padding 9px 16px  

// header区域
@headerHeight:2.125em; //34
@headerFontSize:.75em; //12
@headerPadding:.5em 1em 0 1em;
@headerLinkHeight:2.83em; //34
@headermultiSelectPadding:.4em 0 0 .8em;
// content区域
@contentHeight:2.125em; //34
@contentFontSize:.75em; //12
@contentTdMargin:0 1em 0 1em;
@contentLeftDivHeight:2.83em;
@contentTdDivHeight:2em; //24
@contentmultiSelectPadding: 0 0 0 .8em;
// columnMenu区域
@columnMenuHeight:2.125em; //34
@columnMenuFontSize:.75em; //12
@columnMenuPadding:.5em 1em 0 1em;
@columnMenuLinkHeight:2.83em; //34



.ufida-grid{
    display: block;
    position:relative;
    color:#444;
    background-color:#F9F9F9;
    zoom:1;
    font-family:'Helvetica Neue', Helvetica, 'Hiragino Sans GB', 'Segoe UI', 'Microsoft Yahei', Tahoma, Arial, STHeiti, sans-serif;
    font-weight:normal;
    font-size:16px;
    border-width:1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-shadow:none;
    -webkit-box-shadow:none;
}

.ufida-grid-top{
    display: none;
    position: fixed;
    width:100%;
    height:100%;
    left:0px;
    top:0px;
    z-index: 99999;
    background-color: #fff ;
    filter:alpha(opacity=10); 
    -moz-opacity:0.1;
    -khtml-opacity:0.1; 
    opacity: .1;
    user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-user-drag: none;
    cursor: default;
}

.ufida-grid table{
    width: 100%;
    margin:0;
    max-width:none;
    border-collapse:separate;
    border-spacing:0;
    empty-cells:show;
    border-width:0;
    outline:0;
    table-layout: fixed;
}

.ufida-grid-grid{
    
}
/*
header区域css begin
*/

.ufida-grid-header{
    height: @headerHeight;
    border-style: solid;
    border-width: 0 0 1px 0;
    border-color: #e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-image: none;
    background-position: 50% 50%;
    background-repeat: repeat;
}

.ufida-grid-header-wrap{
    position:relative;
    width:100%;
    overflow:hidden;
    zoom:1;
}
.ufida-grid-header-left{
    position: absolute;
    z-index:11;
    background-color:#F9F9F9;
}
.ufida-grid-header-multi-select{
    padding: @headermultiSelectPadding;
    position:relative;
    float: left;
    height:@headerHeight;
    border-style: solid;
    border-color: #e5e5e5;
    border-width:0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-header-num{
    position:relative;
    float: left;
    height:@headerHeight;
    border-style: solid;
    border-color: #e5e5e5;
    border-width:0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-header-th{
    position: relative;
    vertical-align:bottom;
//  padding:.8em .6em .8em .6em;
    height:@headerHeight;
    font-weight:normal;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    text-align:left;
    border-style:solid;
    border-color:#e5e5e5;
    border-width:0 0 0 1px;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background-image:none;
    background-position:50% 50%;
    user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-drag: none;
  cursor: default;
    
}
.ufida-grid-header-th-first{
    border-left-width:0;
}
.ufida-grid-header-link{
    display:block;
    padding-left:1.33em;
    padding-right:1.33em;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    color:#a8a8a8;
    font-size: @headerFontSize;
    height:@headerLinkHeight;
    line-height:@headerLinkHeight;
}

.ufida-grid-header-link:hover{
    color:#a8a8a8;
}
.ufida-grid-header-link:visited{
    color:#a8a8a8;
}

.ufida-grid-header-columnmenu{
    position:absolute;
    padding:1em .7em .5em .6em;
    font-size:@headerFontSize;
    top:0px;
    right: 0px;
    color:#a8a8a8;
    height:100%;
}
/*拖动*/
.ufida-grid-header-resize-handle{
    position:absolute;
    cursor:col-resize;
    z-index:12;
    width:.6em;
    height: @headerHeight;
    display: block;
    top: 0px;
}
/*交换*/
.ufida-grid-header-drag-clue{
    overflow:hidden;
    border-radius:2px;
    background-color:#3f51b5;
    position:absolute;
    z-index:13;
    border-style:solid;
    border-width:1px;
    font-size:@headerFontSize;
    white-space:nowrap;
    cursor:default;
    color: #fff;
}

.ufida-grid-header-drag-status{
    width: 1em;
    height: 1em;
}


.ufida-grid-header-swap-tip-span{
    height:5px;
    position:absolute;
    z-index:13;
    cursor:default;
    display: none;
    line-height: 0px;
}

/*排序*/
.ufida-grid-header-sort-span{
    width:1em;
    text-align: center;
}
/*
header区域css end
*/

/*
content区域css begin
*/

.ufida-grid td{
    border-style:solid;
    border-color:#e5e5e5;
    border-width:0 0 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    vertical-align:middle;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    height:@contentHeight;
}

.ufida-grid-content-td-div{
    margin: @contentTdMargin;
    height:@contentTdDivHeight;
    line-height: @contentTdDivHeight;
    font-size:@contentFontSize;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
}

.ufida-grid-content{
    position:relative;
    width:100%;
    height:100%;
    overflow-x:hidden;
    overflow-y:hidden;
    zoom:1;
    background-color:#fff ;
}

.ufida-grid-content-div{
    position:relative;
    overflow-x: auto;
    overflow-y: auto;
}

.ufida-grid-content-left{
    position:absolute;
}

.ufida-grid-content-left-sum-bottom{
    position:absolute;
    bottom:16px;
    left:0px;
    height: @contentHeight;
    z-index: 11;
    background-color:#FFF;
}

.ufida-grid-content-left-bottom{
    position:absolute;
    bottom:0px;
    left:0px;
    height: 16px;
    z-index: 11;
    background-color:#FFF;
}
.ufida-grid-content-left-row{
    position: relative;
    height: @contentHeight;
}

.ufida-grid-content-multiSelect{
    padding:@contentmultiSelectPadding;
    position:relative;
    float: left;
    height:@contentHeight;
    border-style: solid;
    border-color: #e5e5e5;
    border-width:0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-content-num{
    border-style: solid;
    border-color: #e5e5e5;
    border-width: 0 1px 1px 0;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    height:@contentLeftDivHeight;
    line-height:@contentLeftDivHeight;
    font-size: @contentFontSize;
    position:relative;
    float:left;
    text-align: center;
    padding: 0px;
}

.ufida-grid-content-sum-row{
    
}

.ufida-grid-content-tree-span{
    width:1.25em;
    height: 1.25em;
    color: #007ae3;
    font-size: 14px;
}

/*
content区域css end
*/


/*
content edit menu区域css begin
*/
.ufida-grid-content-edit-menu{
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: 13;
    background: #fff;
    -webkit-box-sizing:content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}

.ufida-grid-content-edit-menu-button{
    height: 2em;
    margin:.2em .2em;
}

.ufida-grid-noRowsDiv{
    position: relative;
    top:0px;
    left:0px;
    width: 100%;
    font-size: @contentFontSize;
    height: @contentHeight;
    line-height: @contentHeight;
    color:#a8a8a8;
    text-align: center;
}

.ufida-grid-noRowsShowDiv{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    font-size: @contentFontSize;
    height: @contentHeight;
    line-height: @contentHeight;
    color:#a8a8a8;
    text-align: center;
}
/*
content edit menu区域css end
*//*
columnMenu区域css begin
*/
.ufida-grid-column-menu{
    width:160px;
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: 13;
    -webkit-box-sizing:content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}
.ufida-grid-column-menu-ul{
    background-color: #fff;
    font-weight:500;
    background-image: none;
    padding: 0px;
    background-position: 50% 50%;
    border-width: 0px 0px 1px 0px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;

}

.ufida-grid-column-menu-li{
    display: block;
    height: @columnMenuHeight;
    border-width:1px 1px 0px 1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-column-menu-div1{
    display: block;
    padding: 0 .8em 0 .8em;
    line-height: @columnMenuLinkHeight;
    font-size:@columnMenuFontSize;
    position: relative;
    height: 100%;
}  

.ufida-grid-column-menu-span{
    position: absolute;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ufida-grid-column-menu-div3{
    right: 2em;
    position: absolute;
    font-size:@columnMenuFontSize;
    filter:alpha(opacity=70); 
    -moz-opacity:0.7;
    -khtml-opacity:0.7; 
    opacity: .7;
    line-height: @columnMenuLinkHeight;
}
/*
columnMenu区域css end
*/

/*
columnMenu columns区域css begin
*/
.ufida-grid-column-menu-columns{
    width:160px;
    margin-left:-2px;
    padding-left: 2px;
    padding-right:2px;
    padding-bottom: 4px;
    display: none;
    overflow: hidden;
    position: absolute;
    z-index: 12;
    -webkit-box-sizing:content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
}
.ufida-grid-column-menu-columns-ul{
    border-width: 1px 0px 1px 0px;
    border-style: solid;
    border-color:#e5e5e5;
    background-color: #fff;
    font-weight:500;
    background-image: none;
    padding: 0px;
    background-position: 50% 50%;
}

.ufida-grid-column-menu-columns-li{
    display: block;
    height: @columnMenuHeight;
    border-width:0px 1px 0px 1px;
    border-style: solid;
    border-color:#e5e5e5;
    box-sizing:border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.ufida-grid-column-menu-columns-div1{
    display: block;
    padding: 0 .8em 0 .8em;
    line-height: @columnMenuLinkHeight;
    font-size:@columnMenuFontSize;
    position: relative;
    height: 100%;
}  

.ufida-grid-column-menu-columns-div2{
    position: absolute;
    filter:alpha(opacity=70); 
    -moz-opacity:0.7;
    -khtml-opacity:0.7; 
    opacity: .7;
}   

.ufida-grid-column-menu-columns-span{
    position: absolute;
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    left:35px;
}
/*
columnMenu columns区域css end
*/

/*
form展示区域css begin
*/
.ufida-grid-form{
    width:100%;
    height:100%;
    overflow-x: hidden;
    overflow-y: scroll; 
}
/*
form展示区域css end
*/